:root {
  --grid-gap: 1rem;
  --spacer: 1rem;
  --label-margin: 0.25rem;
  --legend-margin: calc(var(--label-margin) * 2);
  --input-padding: 1rem;
  --input-border-radius: 0.25rem;
  --input-border-color: lightgray;
  --input-border-color-focus: dodgerblue;
  --input-border-color-error: crimson;
  --input-border-color-selected: mediumseagreen;
  --input-box-shadow-focus: rgba(black, 0.25) 0 3px 6px -3px;
  --button-bg-color: mediumseagreen;
  --button-bg-color-focus: seagreen;
  --button-color: white; }

body {
  font-size: 1.2rem; }

.App {
  padding: var(--spacer);
  margin: 2rem auto;
  max-width: 1000px; }

* {
  box-sizing: border-box; }

.visually-hidden, input[type="radio"] {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.v-spacing > * + * {
  margin-top: var(--spacer); }

.section-margin + * {
  margin-top: calc(var(--spacer) * 2); }

legend {
  display: block;
  font-size: 1.25em;
  font-weight: bold;
  margin-bottom: var(--legend-margin);
  padding: 0; }

fieldset {
  border: 0;
  margin: 0;
  min-width: 0;
  padding: 0.01em 0 0 0;
  text-align: left; }

label {
  display: block;
  font-size: inherit;
  margin-bottom: var(--label-margin); }

input {
  font-size: inherit;
  outline: none; }

input[type="text"],
input[type="number"],
input[type="email"],
input[type="submit"] {
  border: 1px solid var(--input-border-color);
  border-radius: var(--input-border-radius);
  display: block;
  padding: var(--input-padding);
  width: 100%;
  -webkit-appearance: none; }
  input[type="text"]:focus,
  input[type="number"]:focus,
  input[type="email"]:focus,
  input[type="submit"]:focus {
    box-shadow: rgba(0, 0, 0, 0.25) 0 3px 6px -3px;
    border-color: var(--input-border-color-focus); }

input[type="submit"] {
  border-color: var(--button-bg-color);
  background-color: var(--button-bg-color);
  color: var(--button-color);
  cursor: pointer;
  padding: var(--input-padding) calc(var(--input-padding) * 2);
  width: auto; }
  input[type="submit"]:focus {
    background-color: var(--button-bg-color-focus);
    border-color: var(--button-bg-color-focus);
    box-shadow: rgba(0, 0, 0, 0.25) 0 3px 6px -3px; }

input[type="radio"] + label {
  cursor: pointer; }

.has-error label,
.has-error input {
  color: var(--input-border-color-error); }

.has-error input {
  border-color: var(--input-border-color-error); }

.input-group {
  display: flex;
  position: relative; }
  .input-group > * {
    border-radius: var(--input-border-radius); }
    .input-group > * + * {
      margin-left: -1px; }
    .input-group > *:first-child {
      border-bottom-right-radius: 0;
      border-top-right-radius: 0; }
    .input-group > *:last-child {
      border-bottom-left-radius: 0;
      border-top-left-radius: 0; }
    .input-group > *:not(:first-child):not(:last-child) {
      border-radius: 0; }
  .input-group input {
    flex: 1 1 auto;
    min-width: 0; }
  .input-group label {
    background-color: #ebebeb; }

.gift-card-amount__options label {
  border: 1px solid var(--input-border-color);
  margin-bottom: 0;
  padding: var(--input-padding); }

.gift-card-amount__options input[type="radio"] + label {
  border-radius: var(--input-border-radius);
  font-weight: bold;
  margin-bottom: var(--grid-gap);
  text-align: center; }

.gift-card-amount__options input[type="radio"] + label:hover {
  border-color: var(--input-border-color-focus);
  box-shadow: rgba(0, 0, 0, 0.25) 0 3px 6px -3px; }

.gift-card-amount__options input[type="radio"]:checked + label {
  color: var(--input-border-color-selected);
  border-color: var(--input-border-color-selected);
  box-shadow: none; }

.gift-card-amount__options input[type="radio"]:checked:focus + label {
  color: var(--input-border-color-selected); }

.gift-card-amount__options input[type="radio"]:focus + label {
  color: black;
  border-color: var(--input-border-color-focus);
  box-shadow: rgba(0, 0, 0, 0.25) 0 3px 6px -3px; }

.form-group {
  display: flex;
  flex-wrap: wrap;
  margin: 0 calc(var(--grid-gap) * -1) calc(var(--grid-gap) * -1);
  padding: calc(var(--grid-gap) / 2); }
  .form-group > * {
    flex: 1 0 auto;
    margin: 0 calc(var(--grid-gap) / 2) var(--grid-gap);
    max-width: 100%; }

.flex-equal-widths {
  display: block; }
  @media (min-width: 750px) {
    .flex-equal-widths {
      display: flex; }
      .flex-equal-widths > * {
        flex: 1 1 0px; } }

[type="radio"].radio-trigger {
  clip: unset;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0; }

.additional-description {
  color: gray;
  display: block;
  font-size: 0.8em;
  margin-bottom: var(--label-margin); }

.error-message {
  color: var(--input-border-color-error);
  display: block;
  font-size: 0.8em;
  margin: var(--label-margin) 0; }
